<template>
    <div class="nav">
        <div @click='toMe'>
            <img :src="shouye" alt="">
            <p :class="{active: isActive}">首页</p>
        </div>
        <div @click='toHome'>
            <img :src="wode" alt="">
            <p>我的</p>
        </div>        
    </div>
</template>

<script>
import shouyeb from 'img/shouyeb.png'
import wodea from 'img/wodea.png'

export default {
    data () {
        return {
            shouye: shouyeb,
            wode: wodea,
            isActive: true
        }
    },
    methods: {
        toMe() {
            this.$router.push('/')
        },
        toHome() {
            this.$router.push('/Me')
        }
    }
}
</script>

<style scoped>
@import '../../../assets/css/public.css'


</style>


