            </div>
            <p>登录</p>
        </div>  
        <div class="header-img">
            <img src="../../../assets/img/fengmian.jpeg" alt="">
        </div>      
    </div>
</template>

<script>
export default {
    methods: {
        toMe () {
            this.$router.push('/Me')
        }
    }
}
</script>

<style scoped>
@import '../../../assets/css/public.css';

.header-img img{
    width: 100%;
}

</style>


