<template>
    <div class="register">
        <form>
            <input type="text" value="" placeholder="请输入手机号"
                v-model="phone"
            >
            <div class="send">
                <input type="text" placeholder="请输入验证码"
                    v-model="code"
                >
                <!-- 验证码按钮 -->
                <input type="button"
                    @click="seedCode"               
                    :value="codeText"
                    :disabled="send"
                    :class="{'btnActiveColor': sendcode, 'btnActive': codein,'send-btn':funshow}"
                    v-bind="teSting"
                    v-show="!test"
                >
                <!-- 验证手机号码的 错误提示按钮 -->
                <input type="button" class="send-btn"
                    :value="codeText"
                    @click="testCode"
                    v-show="test"
                >
            </div>
            <input type="password" placeholder="请输入密码"
                v-model="password"
            >
            <div class="register-btn">
                <input type="button" value="注册并登录"
                    v-bind="registerIng" :disabled="isShow"
                    :class="{'isActiveColor': sendregis,'isActive': regisin}"
                    @click="postRegister"
                >
            </div>
        </form>
    </div>
</template>

<script>
import cookie from '../../../axios/cookie.js'
import { getcode, register } from '../../../api/api'

export default {
    data() {
        return {
            phone: '',
            code: '',
            password: '',
            isShow: false,      // 强制 input不生效
            sendregis: false,   // 输入框为空，背景不高亮
            regisin: true,      // 输入框不为空，背景高亮
            codeText: '发送验证码',
            // 验证码
            send: false,        // 强制 input不生效
            funshow: true,      // send常用样式
            sendcode: true,     // send按钮点击前，背景高亮
            codein: false,      // send按钮点击后，背景不高亮
            test: true,         // 判断显示哪个验证码按钮
            phones: ''          // 验证错误提示内容
        }
    },
    methods: {
        // 发送验证码
        seedCode() {
            // console.log(111)
            var that = this;    // 将指向按钮的this 指向全局
            var countdown = 60;
            codetime();

            function codetime() {
                // console.log(222)
                if(countdown == 0) {
                    that.codeText = '发送验证码';                    
                    countdown = 60;                    
                    that.send = false;
                    // 改变验证码按钮背景条件
                    that.sendcode = true;
                    that.codein = false;
                    return;
                } else {
                    countdown--;
                    that.codeText= countdown + "秒后重发";
                    that.send = true;
                    // 改变验证码按钮背景条件                    
                    that.sendcode = false;
                    that.codein = true;
                }
                setTimeout(function() {
                    codetime();
                },1000)
            }
            // 发送验证码
            getcode({
                mobile: that.phone
            }).then((res) => {

            }).catch((error) => {
                // 手机号 错误提示信息 (手机号码已经存在)
                that.phones = error.mobile[0];
                that.showToast()
            })
        },

        // 点击按钮注册
        postRegister() {
            register({
                username: this.phone,
                code: this.code,
                password: this.password
            }).then((res) => {
                console.log(res)
                // 用cookie 将数据本地存储7天
                cookie.setCookie('name',res.data.username,7); // 名称
                cookie.setCookie('token',res.data.token,7); // token (识别用户)
                this.$store.dispath('users')
                // 更新vuex数据
                this.$store.dispatch('users')
                // 跳转首页
                this.$router.push('/')                
            }).catch((error) => {
                console.log(error)
                this.phone = error.mobile[0];
                this.code = error.code[0];
                this.showToast();
            })
        },

        testCode() {
            this.phones = '请输入正确的手机号码';   // 自定义弹窗的内容
            this.showToast();   // 调用自定义弹窗组件
        },

        // 自定义弹窗组件 使用在谁身上
        showToast() {
            this.$popup(this.phones)
        }        
    },
    computed: {
        // 监听 input框是否为空
        registerIng() {
            if(this.phone && this.code && this.password != "") {
                this.isShow = false;
                // 改变背景高亮条件 
                this.sendregis = true;
                this.regisin = false;
                // 改变显示哪个验证码按钮条件
                this.test = true;
            } else {
                this.isShow = true;
                // 改变背景高亮条件 
                this.sendregis = false;
                this.regisin = true;
                // 改变显示哪个验证码按钮条件
                this.test = false;
            }
        },
        // 监听 send按钮的手机号码是否正确
        teSting() {
            if(!(/^1(3|4|5|7|8)\d{9}$/.test(this.phone))) {
                this.send = true;
                // 改变验证码按钮背景条件 
                this.sendcode = false;
                this.codein = true;
            } else {
                this.send = false;
                // 改变验证码按钮背景条件 
                this.sendcode = true;
                this.codein = false;
            }
        }
    }
}
</script>

<style scoped>
input {
    width: 100%;
    display: block;
    padding: .2rem 0;
    border: .02rem solid #909090;
    border-radius: .08rem;
    text-indent: .16rem;
}

.register {
    padding: 0 .5rem;
}

.send {
    display: flex;
    margin: .4rem 0;
}

.send input:first-child {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border-right: 0;
}

.send-btn {
    width: 50%;
    border-left: 0;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    border: 0;
    background: #3091e7;
    color: #fff;
    text-align: center;
    color: .32rem; 
}

.register-btn {
    margin-top: .8rem;
}

.register-btn input {
    border-radius: 1rem;
    border: 0;
    font-size: .4rem;
    text-align: center;
    color: #fff;
}

.isActiveColor {
    background: #02bf03;
}

.isActive {
    background: #ccc;
}

.btnActiveColor {
    background: #3091e7;
}

.btnActive {
    background: #ccc;
}


</style>


