<template>
    <div class="fn">
        <div class="manual">
            <div class="collection">
                <div v-if="isImgShow">
                    <img src="../../../assets/img/weishoucang.png" />
                    <p>未收藏</p>
                </div>
                <div v-if="!isImgShow">
                    <img src="../../../assets/img/yishou.png" />
                    <p>已收藏</p>
                </div>
            </div>
            <div class="play">
                <div class="play-left">
                    <p>{{ this.title }}</p>
                    <p>{{ this.sketch }}</p>
                </div>
                <div class="play-right">
                    <img src="../../../assets/img/bofang.png"
                        v-if="playing" @click="plays()" 
                    />
                    <img src="../../../assets/img/zanting.png" 
                        v-if="suspend" @click="supende()"
                    />
                    <audio :src="this.aispeech" ref="audio" @ended="songend"></audio>
                </div>
            </div>       
        </div>
        <div class="article" ref="box">
            <div v-html="this.article"></div>
        </div>
    </div>
</template>

<script>
import { speechend } from '../../../api/api'
// import cookit from '../../../axios/cookie.js'

export default {
    // 接收父组件details 传入的值
    props: {
        title: '',
        sketch: '',
        article: ''
    },
    data () {
        return {
            isImgShow: true,
            playing: true,
            suspend: false,
			textcon:'',
			aispeech:''
        }
    },
    methods: {
        plays() {
            var Audio = this.$refs.audio;
            Audio.play();
            this.playing = false;
            this.suspend = true;
        },
        supende() {
            var Audio = this.$refs.audio;
            Audio.pause();
            this.playing = true;
            this.suspend = false;
        },
        songend() {
            this.playing = true;
            this.suspend = false;
        },
		voIce(){
            var box = this.$refs.box;
            var con = box.textContent || box.innerText;
			// console.log(con)
            this.textcon = con;
            console.log(this.textcon)
			const formData = new FormData();
            formData.append('speechen', this.textcon);
			speechend(formData).then((res) => {
                console.log(res)
				this.aispeech = res.data.speeching
			}).catch((error) => {
                console.log(error)
            })
		}
    },
    watch: {
		article:function(){
			this.$nextTick(function(){
                // console.log('文章渲染完成了')
                
				// this.voIce()
			})
		}
    }

}
</script>

<style scoped>
.fn{
    margin: .2rem .14rem;
}
.manual {
    padding: .2rem .4rem;  
    display: flex;
    justify-content: space-between;
    font-size: .3rem;    
    background: #fff;
    border-radius: .1rem;
}

.manual img{
    width: .7rem;
}

.collection {
    text-align: center; 
    padding-left: .08rem;   
    padding-right: .4rem;

}

.collection p {
    padding-top: .1rem;

}

.play {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-left: .02rem solid #ccc;
}

.play-left {
    width: 3.6rem;
    padding-left: .3rem;
}

.play-left p {
    line-height: 1.5;
    font-size: .24rem;
    color: #888; 
}

.play-left p:nth-child(1) {     
    overflow: hidden;    
    white-space: nowrap;    
    text-overflow: ellipsis;     
    font-size: .4rem;      
    color: #333;
   
}

.play-right {
    padding-left: .4rem;
    
}

.article {
    margin-top: .3rem;
    line-height: 1.6;
}






</style>























