<template>
<div>
    <div class="name">
        <div class="btn" @click="toLogin" v-if="!this.userInfo.name">
            登录
        </div>
        <div v-else>
            <img src="../../../assets/img/zhangguo.jpg" alt="">
            <p>{{ this.userInfo.name }}</p>
        </div>
    </div>
</div>

</template>

<script>
import { mapState } from 'vuex'

export default {
    methods: {
        toLogin() {
            this.$router.push('/Login')
        }
    },
    computed: {
        ...mapState([
            'userInfo'
        ])
    }
}
</script>


<style scoped>
.name {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: .6rem 0;
    background: #477bc7;
}

.btn {
    width: 2rem;
    border-radius: .1rem;
    padding: .2rem .1rem;
    color: #fff;
    background: yellowgreen;
}

.name img {
    width: 1.6rem;
    border-radius: 50%;
}

.name p {
    font-size: .36rem;
    color: #fff;
    margin-top: .4rem;
    /* font-weight: bold; */
}

</style>














