<template>
    <div class="details">
        <Details-Header
            :title = 'title'
        >
        </Details-Header>
        <Details-Banner
            :title = 'title'
            :imgUrl = 'imgUrl'
            :imgLength = 'imgLength'            
        >
        </Details-Banner>
        <Details-Swiper
            :imgLength = 'imgLength'
        >            
        </Details-Swiper>
        <Details-Manual
            :title = 'title'
            :sketch = 'sketch'
            :article = 'article'
        >
        </Details-Manual>
        <!-- <div style="height: 2000px;"></div> -->
    </div>
</template>

<script>
import DetailsHeader from './pages/Header'
import DetailsBanner from './pages/Banner'
import DetailsSwiper from './pages/Swiper'
import DetailsManual from './pages/Manual'


import { details } from '../../api/api'

export default {
    data () {
        return {
            detailsId: '',  // 当前页的id值
            title: '',
            imgUrl: '',
            imgLength: [],
            sketch: '',
            article: ''
        }
    },
    mounted() {
        details(this.detailsId).then((res) => {
            var data = res.data;
            // console.log(data);
            this.title = data.curriculum;
            this.imgUrl = data.image;
            this.imgLength = data.images;
            this.sketch = data.lecturer;
            this.article = data.goods_desc;
        })
    },
    components: {
        DetailsHeader,
        DetailsBanner,
        DetailsSwiper,
        DetailsManual
    },
    created() {
        // 将列表页的id值 赋值 给当前页的id
        this.detailsId = this.$route.params.id;
    }


    
}
</script>

<style scoped>
.details {
    background: #f5f5f5;
}

</style>











