<template>
    <div class="header">
        <div class="header-return" v-if="isShow" @click='toHome'>
            <img src="../../../assets/img/fanhui.png" alt="">
        </div>
        <div class="header-top" v-if="!isShow" :style="styleOpacity">
            <div class="header-left" @click='toHome'>
                <img src="../../../assets/img/fanhui.png" alt="">
            </div>
            <p>{{ this.title }}</p>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        title: ''
    },
    data() {
        return {
            isShow: true,
            styleOpacity: {
                opacity: 0.5
            }
        }
    },
    methods: {
        toHome() {
            this.$router.go(-1)
        }
    },
    mounted () {
        let That = this;
        window.addEventListener("scroll",function() {
            let top = document.documentElement.scrollTop || document.body.scrollTop;
            if(top > 45) {
                let opacity = top/130;
                opacity = opacity > 1 ? 1 : opacity;
                That.styleOpacity = { opacity };
                That.isShow = false;
            } else {
                That.isShow = true;
            }
        })
    }
}
</script>

<style scoped>
@import '../../../assets/css/public.css';

.header-return {
    position: absolute;
    left: .2rem;
    top: .18rem;
    width: .72rem;
    height: .72rem;
    line-height: .72rem;
    background: rgba(0,0,0,.6);
    border-radius: 50%;
    text-align: center;
    z-index: 99;
}

.header-return img {
    width: .5rem;
    color: #fff;
}

</style>












