<template>
    <div class="details-swiper" v-if="showSwiper" @click="toBanner">
        <div class="img-swiper">

            <swiper :options="swiperOption">
                <swiper-slide
                    v-for="item in imgLength" :key="item.id"
                >
                    <img :src="item.image" alt="">
                </swiper-slide>

                <div class="swiper-pagination" slot="pagination"></div>

            </swiper>

        </div>
    </div>
</template>

<script>
import Bus from '../../../bus/bus'

export default {
    props: {
        imgLength: Array
    },
    data() {
        return {
            swiperOption: {
                pagination: {
                    el: '.swiper-pagination',
                    type: 'fraction' 
                }            
            },
            showSwiper: false
        }
    },
    mounted() {
        // 将自己的值传给兄弟组件
        Bus.$emit('val', this.showSwiper)
        // 接收兄弟组件传的值
        Bus.$on('val',(data) => {
            this.showSwiper = data
        })
    },
    methods: {
        toBanner() {
            this.showSwiper = false;
        }
    }
}
</script>

<style scoped>
.details-swiper {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 99;
    background: #000;
}

.img-swiper {
    width: 100%;
    height: 0;
    overflow: hidden;
    padding-bottom: 100%;
}

.swiper-pagination {
    position: fixed;
    bottom: 1rem;
    color: #fff;
    font-size: .32rem;
}

.details-swiper >>> .swiper-pagination-bullet-active {
    background: #fff;    
}

</style>













