<template>
<div>
    <div class="switch">
        <div @click="toLoginend">登录</div>
        <div @click="toRegister">注册</div>
    </div>

    <router-view></router-view>

</div>
</template>

<script>
export default {
    data() {
        return {
            isShow: true,            
        }
    },
    methods: {
        toLoginend() {
            this.$router.push('/Loginend')
            this.isShow = true       
        },
        toRegister() {
            this.$router.push('/Register')
            this.isShow = false
        }
    }
}
</script>

<style scoped>
.switch{
    display: flex;
    justify-content: space-around;
    margin: .7rem 0;
}

.switch div {
    font-size: .44rem;
}

.isActive {
    color: #6cd868;
}

</style>


