<template>
    <div class="status" v-if="this.userInfo.name">
        <div>
            <img src="../../../assets/img/myshou.png" alt="" />
            <p>我的收藏</p>
        </div>

        <div @click="loGout">
            <img src="../../../assets/img/tuichu.png" alt="" />
            <p>退出登录</p>
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'
import cookie from '../../../axios/cookie.js'

export default {
    computed: {
        ...mapState([
            'userInfo'
        ])
    },
    methods: {
        loGout() {
            cookie.delCookie('token');
            cookie.delCookie('name');
            this.$store.dispatch('users')
            // 更新vuex数据
            this.$store.dispatch('users')
            // 跳转首页
            this.$router.push('/')            
        }
    }
}
</script>

<style scoped>
.status {
    display: flex;
    justify-content: space-between;
    margin: .3rem .3rem;
}

.status div {
    width: 46%;
    border-radius: .02rem;
    color: #fff;
    font-size: .2rem;
    text-align: center;
    padding: .2rem 0;
    background: #3aa4f0;
}

.status img {
    width: .7rem;
    padding-bottom: .1rem;
}




</style>













