<template>
    <div class="loginend">
        <form>
            <input type="text" value="" placeholder="请输入手机号"
                v-model="phone" 
            />             
            <input type="text" value="" />              
            <input type="password" value="" placeholder="请输入密码"
                v-model="password"
            />
            <input type="button" value="登录" 
                v-bind="loginIng" :disabled="isShow"
                :class="{'isActiveColor': sendregis, 'isActive': sendregisin }"
                @click="loGin"
            />
           
        </form>
    </div>
</template>

<script>
import { login } from '../../../api/api'
import cookie from '../../../axios/cookie'

export default {
    data () {
        return {
            phone: '',
            password: '',
            isShow: true,   // 强制input不生效
            sendregis: true,    // 输入框为不空,背景高亮            
            sendregisin: false,  // 输入框为空，背景不高亮
            phones: ''
        }
    },
    computed: {
        loginIng() {
            if(this.phone && this.password != "") {
                this.isShow = false;
                // 改变背景高亮条件
                this.sendregis = true;
                this.sendregisin = false;
            } else {
                this.isShow = true;
                // 改变背景高亮条件                
                this.sendregis = false;
                this.sendregisin = true;
            }
        }
    },
    methods: {
        loGin() {
            var that = this;
            login({
                username: that.phone,
                password: that.password
            }).then((res) => {
                cookie.setCookie('name',that.phone,7);
                cookie.setCookie('token',res.data.token,7);
                // 更新vuex数据
                this.$store.dispatch('users')
                // 跳转首页
                this.$router.push('/')
            }).catch((error) => {
                if('non_field_errors' in error) {
                    that.phones = '手机号或密码错误'
                    that.showToast()
                    console.log(error)
                }
            })
        },

        // 自定义弹窗组件
        showToast() {
            this.$popup(this.phones)
        }
    }
}
</script>

<style scoped>
input {
    display: block;
    width: 100%;
    border: .02rem solid #909090;
    padding: .2rem 0;
    border-radius: .08rem;
    text-indent: .16rem;
}

.loginend {
    margin: 0 .5rem;
}

.loginend input:first-child {
    margin: .4rem 0;
}

.loginend input:nth-child(2) {
    display: none;
}

.loginend input:last-child {
    margin-top: .8rem;
    text-align: center;
    border: 0;
    border-radius: 1rem;
    font-size: .4rem;
    color: #fff;
}

.isActiveColor {
    background: #02bf03;
}

.isActive {
    background: #ccc;
}

</style>


