<template>
    <div class="nav">
        <div @click='toMe'>
            <img :src="shouye" alt="">
            <p>首页</p>
        </div>
        <div @click='toHome'>
            <img :src="wode" alt="">
            <p :class="{active: isActive}">我的</p>
        </div>        
    </div>
</template>

<script>
import shouyea from 'img/shouyea.png'
import wodeb from 'img/wodeb.png'

export default {
    data () {
        return {
            shouye: shouyea,
            wode: wodeb,
            isActive: true
        }
    },
    methods: {
        toMe() {
            this.$router.push('/')
        },
        toHome() {
            this.$router.push('/Me')
        }
    }    
}
</script>

<style scoped>
@import '../../../assets/css/public.css'


</style>


