<template>
    <div class="home-list">
        <div v-for="item in homeList" :key='item.id'>
            <router-link :to="'/Details/' + item.id">
                <div class="content">
                    <div class="content-img">
                        <img :src="item.image" alt="">
                    </div>
                    <div class="content-text">
                        <div class="content-title">
                            <h3>{{ item.name }}</h3>
                        </div>
                        <div class="content-tag">
                            <p>{{ item.title }}</p>
                        </div>
                    </div>
                </div>
            </router-link>
        </div>
        <div class="loading" v-show="load">
            {{ this.loadText }}
        </div>
    </div>
</template>

<script>
import { homeList } from '../../../api/api'

export default {
    data () {
        return {
            homeList: [],
            load: false,
            loadText: '努力加载中...',
            pageId: 1
        }
    },
    methods: {
        homeListData() {
            homeList(this.pageId).then((res) => {                 
                if(res.data.results != null) {
                    if(this.pageId == 1) {
                        this.homeList = res.data.results;  
                        // console.log(111)                                                    
                    } else {
                        this.homeList = this.homeList.concat(res.data.results);  
                        // console.log(222)                          
                    }
                }
            })
        }
    },    
    mounted () {
        this.homeListData()
        // 上拉加载数据
        window.onscroll = () =>{
            // 滚动条滚动时距离顶部的距离
			var scrollTop = parseInt(document.documentElement.scrollTop || document.body.scrollTop);
            // console.log(scrollTop)

            // 可视化区域的高度
            var windowHeight = document.documentElement.clientHeight || document.body.clientHeight;
            // console.log(windowHeight)  

            // 滚动条的总高度       
            var scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight;
            // console.log(scrollHeight)    

            if(scrollTop + windowHeight == scrollHeight - 1 ){                   
                // console.log("到底部了")
                this.pageId++
                // console.log(this.pageId)
                this.load = true
                if(this.pageId < 5){
                    this.homeListData()
                }else{
                    this.loadText = "已经到底啦！没有更多数据了..."
                    this.load = true
                }
            }
        }
    }
}
</script>

<style scoped>
@import '../../../assets/css/public.css';

.loading {
    text-align: center;
    padding: .4rem 0 .2rem;
    color: #ccc;
    font-size: .32rem;
}

</style>










