<template>
  <div id="app">
    <keep-alive>
      <!-- 缓存页面 -->
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    
    <!-- 不用缓存页面 -->
    <router-view v-if="!$route.meta.keepAlive"></router-view>    

  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>

</style>



