<template>
    <div class="banner">
        <div class="banner-img" @click='imgShow'>
            <img :src="imgUrl" alt="">
        </div>
        <div class="banner-title">
            {{ this.title }}
        </div>
        <div class="banner-imgLength">
            {{ this.imgLength.length }}张图片
        </div>
        <div class="banner-iconList">
            <div>
                <img src="../../../assets/img/jingdian.png" alt="">
                <p>景点</p>
            </div>
            <div>
                <img src="../../../assets/img/meishi.png" alt="">
                <p>美食</p>
            </div>
            <div>
                <img src="../../../assets/img/gouwu.png" alt="">
                <p>购物</p>
            </div>
            <div>
                <img src="../../../assets/img/zhusu.png" alt="">
                <p>住宿</p>
            </div>                                    
        </div>
    </div>
</template>

<script>
import Bus from '../../../bus/bus'

export default {
    // 接收父组件 details传入的值
    props:{
        title: '',
        imgUrl: '',
        imgLength: Array     
    },
    data () {
        return {
            isShow: ''
        }
    },
    mounted() {
        // 接收兄弟组件传的值
        Bus.$on('val', (data) => {
            this.isShow = data
        })
    },
    methods: {
        imgShow() {
            this.isShow = true;
            Bus.$emit('val',this.isShow)
        }
    }
}
</script>

<style scoped>
.banner {
    position: relative;
}

.banner-img img {
    width: 100%;
}

.banner-title {
    position: absolute;
    bottom: 2.24rem;
    left: .5rem;
    font-size: .48rem;
    color: #fff;
}

.banner-imgLength {
    position: absolute;
    bottom: 1.8rem;
    left: .5rem;
    font-size: .32rem;
    color: #fff;
}

.banner-iconList {
    width: 100%;
    display: flex;
    justify-content: space-around;
    position: absolute;
    bottom: .26rem;
    text-align: center;
    color: #fff;
}

.banner-iconList img {
    width: 45%;
}

.banner-iconList p {
    font-size: .3rem;
    padding-top: .1rem;
    color: #fff;
}

</style>










