import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

import Home from '@/page/home/Home'
import Me from '@/page/me/Me'
import Details from '@/page/details/Details'
import Search from '@/page/search/Search'
import Login from '@/page/login/Login'
import Loginend from '@/page/login/pages/Loginend'
import Register from '@/page/login/pages/Register'

export default new Router({
  routes: [
    {
      path: '/',
      component: Home,
      meta: {           
        keepAlive: true     // 缓存页面
      }      
    },
    {
      path: '/Me',
      component: Me
    },
    {
      path: '/Details/:id',
      component: Details,
      meta: {           
        keepAlive: false     // 缓存页面
      }         
    },
    {
      path: '/Search',
      component: Search,
      meta: {
        keepAlive: true     // 缓存页面
      }
    },
    {
      path: '/Login',
      component: Login,
      redirect: '/Loginend',  // 定义默认展示页面
      children: [             // 二级路由
        {
          path: '/Loginend',
          component: Loginend
        },
        {
          path: '/Register',
          component: Register
        }
      ]
    }

    

  ]
})



