import Vue from 'vue'
import axios from 'axios'
import store from 'c:/Users/dell/Desktop/yunnan/src/store/store';

// http requset拦截器
axios.interceptors.request.use(
    config => {
        // 判断是否存在token;如果存在则每个 http header都加上token
        if(store.state.userInfo.token) {
            config.headers.Authorization = `JWT ${store.state.userInfo.toked}`;    
        }
        return config;
    },
    err => {
        return Promise.reject(err);
    }
)

// http response拦截器
axios.interceptors.response.use(
    response => {
        return response;
    },
    error => {
        if(error.response) {
            switch(error.response.status) {
                case 401:
                    console.log("错误了，还未登录")
            }
        }
        //  返回接口返回的错误信息
        return Promise.reject(error.response.data)
    }
)


