<template>
    <div>
        <div class="search">
            <input type="text" autofocus="autofocus" placeholder="请输入云南旅游目的地/攻略"
                v-model="keyworld" @keyup.enter="submit"
            />
            <router-link to='/'>
                <p>取消</p>
            </router-link>        
        </div>
        <div class="home-list" v-if="searchShow">
            <div v-for="item in resule" :key="item.id">
                <router-link :to="'/Details/' + item.id">
                    <div class="content">
                        <div class="content-img">
                            <img :src="item.name.image" alt="">
                        </div>
                        <div class="content-text">
                            <div class="content-title">
                                <h3>{{ item.name.name }}</h3>
                            </div>
                            <div class="content-tag">
                                <p>{{ item.name.title }}</p>
                            </div>
                        </div>
                    </div>
                </router-link>
            </div>
        </div>
        <div class="nosearch" v-else>
            <img src="../../assets/img/rekong.png" alt="">
        </div>
    </div>
</template>

<script>
import { searchIng } from '../../api/api'

export default {
    data () {
        return {
            // 搜索的关键词
            keyworld: '',
            // 搜索结果
            resule: [],
            searchShow: true
        }
    },
    methods: {
        submit() {
            // console.log(this.keyworld);
            searchIng(this.keyworld).then((res) => {
                var searchData = res.data;
                // console.log(searchData);
                if(searchData.length !== 0) {
                    this.resule = searchData;  
                    this.searchShow = true;                  
                } else {
                    this.searchShow = false;
                }
            })
        }
    }
}
</script>

<style scoped>
@import '../../assets/css/public.css';

.search {
    display: flex;
    height: 1rem;
    align-items: center;
    padding: 0 .2rem;
}

.search input {
    height: .5rem;
    width: 100%;
    border-radius: 1rem;
    padding: .06rem .3rem;
    font-size: .26rem;
    background: #ececec;
}

.search p {
    width: 1.2rem;
    height: .5rem;
    text-align: center;
    line-height: .5rem;
    color: #f39c11;
    margin-left: .2rem;
}

.nosearch {
    text-align: center;
}

.nosearch img {
    width: 60%;
}


</style>












