import axios from 'axios'

let api = 'http://vue.thexxdd.cn'

// sweiper
export const swiper = () => {
    return axios.get((api + '/tourism/'));
}

// home-list
export const homeList = (loanend) =>{
	return axios.get((api + "/homes/?page=" + loanend));
}

// details
export const details = (goodsId) => {
    return axios.get((api + '/goods/' + goodsId) + '/');
}

// 语音合成
export const speechend = param =>{
	return axios.post((api + '/speech/'), param);
}

// 搜索
export const searchIng = (searder) => {
    return axios.get((api + '/goods/?search=' + searder));
}

// 发送验证码
export const getcode = param => {
    return axios.post((api + '/codes/'), param);
}

// 注册
export const register = param => {
    return axios.post((api + '/users/'), param);
}

// 登录
export const login = param => {
    return axios.post((api + '/login/'), param)
}


